import React, { Component } from 'react'
import { Link } from "gatsby";
import data from "../../../data/Data";
import ToggleLang from "../../../components/theme/Header/ToggleLang";

class NavLinks extends Component {

    generateNavLinks = (lang, pageNames, pageNamesCN, themeColors, isInSidebar) =>
        <React.Fragment>
            <li className={isInSidebar? 'sidebar-item': 'nav-item'}>
                <ToggleLang lang={lang}/>
            </li>
            { !isInSidebar && <span className='link-separator'>|</span>}
            {pageNames.map((link, idx) =>
                <li className={isInSidebar? 'sidebar-item': 'nav-item'} key={idx}>
                    <Link
                        to={this.getLink(lang, link)}
                        className={isInSidebar? 'sidebar-link' : 'nav-link'}
                        activeClassName='active'
                        partiallyActive={ !(link.toLowerCase()==="home") }
                    >
                        { lang==="en" ? link : pageNamesCN[idx]}
                    </Link>
                    { !isInSidebar && idx !== pageNames.length -1 && <span className='link-separator'>|</span>}
                </li>
            )}
        </React.Fragment>

    getLink = (lang, link) => {
        const langPath = lang==="en" ? "" : "/cn"
        return link.toLowerCase() !== 'home'
            ? (`${langPath}/${link.toLowerCase().replace(/ /g, "_")}/`)
            : (`${langPath}/`)
    }

    render() {
        const pageNames = data.pageNames
        const pageNamesCN = data.pageNamesCN.map(name => name.toLowerCase())
        const {lang, themeColors, isInSidebar} = this.props
        const navLinks = this.generateNavLinks(lang, pageNames, pageNamesCN, themeColors, isInSidebar)
        return (    
            <ul className="nav">
                {navLinks}
            </ul>
        )
    }
}

export default NavLinks;