import React, { Component } from 'react'
import Navlinks from './Navlinks'
import Logo from '../../images/logo.png'
import Link from "gatsby-link";
// import Logo from "../../../components/theme/Header/Logo";

export default class Header extends Component {
    render() {
        const {lang, links, themeColors, isMobileDevice, isSidebarOpen} = this.props
        const navLinks = isMobileDevice
            ? ''
            :   <Navlinks
                    lang={ lang }
                    links={ links }
                    themeColors={ themeColors }
                />

        return (    
            <header id={isSidebarOpen? '' : 'main-header'}>
                <h1 style={{width: "100%", margin:'0'}}>
                    <Link to={ lang==="en" ? "/" : "/cn" } style={{verticalAlign: 'middle'}}>
                        <img src={Logo} alt='logo'/>
                        {/*<Logo />*/}
                    </Link>
                </h1>
                {navLinks}
            </header>
        )
    }
}