import React, { PureComponent } from 'react'
import ReactMarkdown from 'react-markdown'
import "../styles.scss"
import {Seo} from "../../components/common";

export default class Book extends PureComponent {
    componentDidMount () {
        window.scrollTo(0,0)
    }

    render () {
        const {lang, cover, bookData} = this.props
        const isEn = lang==="en"
        const {pageName, seoTitle, seoTitleCN, title, titleCN, publisher, publisherCN, bookLink, preOrderLink, readLink, bookIntroduction, bookIntroductionCN, reviews} = bookData
        const pathname = pageName.toLowerCase().replace(/ /g, "_")

        const moreReviews = reviews && 
                <ul className='more-book-review'>
                    {reviews.map(review => 
                        <li key={review.reviewer} className='review-item'>
                            {/*style={isEn ? {} : {fontFamily: `'STKaiti', 'Kaiti', Helvetica, sans-serif`, fontSize: "20px"}}*/}
                            <div className='book-text'>
                                <ReactMarkdown 
                                    className={review.magazine? 'commentor magazine' : 'commentor'}
                                    key={review.reviewer}
                                    children={ isEn ? review.reviewer : review.reviewerCN}
                                    linkTarget="_blank">
                                </ReactMarkdown>
                                <ReactMarkdown
                                    className="review-content"
                                    key={review.review}
                                    children={ isEn ? review.review : review.reviewCN}
                                    linkTarget="_blank">
                                </ReactMarkdown>
                            </div>
                        </li>
                    )}
                </ul>

        return (
            <div id='maincontent-container'>
                <main className='book'>
                    <Seo title={isEn ? seoTitle : seoTitleCN}
                         description={isEn ? bookIntroduction.join(" ") : bookIntroductionCN.join(" ")}
                         location={isEn ? "/" + pathname : "/cn/" + pathname}
                    />
                    <div className='grid'>
                        <h2 className='bookname'>
                            {isEn ? title: titleCN}
                        </h2>
                        <div className='book-pic-container'>
                            <div className='gap'>
                                <img className='book-img'
                                    src={cover}
                                    alt={ isEn ? seoTitle : seoTitleCN}
                                />
                                <p className='publisher'>{isEn ? publisher : publisherCN}</p>
                                {bookLink &&
                                    <a className='book-link' target="_blank" href={bookLink} rel="noopener noreferrer">
                                        {isEn ? "Available on Amazon" : "亚马逊有售"}
                                    </a>}
                                {preOrderLink && 
                                    <a className='book-link' target="_blank" href={preOrderLink} rel="noopener noreferrer">
                                        {isEn ? "Preorder" : "预购"}
                                    </a>}
                                {readLink &&
                                    <a className='book-link' href={readLink}>
                                        {isEn ? "Read Online" : "在线阅读"}
                                    </a>}
                            </div>
                        </div>
                        {/*style={isEn ? {} : {fontFamily: `'STKaiti', 'Kaiti', Helvetica, sans-serif`, fontSize: "20px"}}*/}
                        <div className='book-introduction'>
                            {isEn && bookIntroduction && bookIntroduction.map((text, idx) =>
                                <ReactMarkdown
                                    className='book-text bigger-first-letter'
                                    key={`${title} introduction ${idx}`}
                                    children={text}
                                />
                            )}
                            {!isEn && bookIntroductionCN && bookIntroductionCN.map((text, idx) =>
                                <ReactMarkdown
                                    className='book-text bigger-first-letter'
                                    key={`${title} introduction ${idx}`}
                                    children={text}
                                />
                            )}
                        </div>

                        {reviews && 
                            <div className='book-review'>
                                <h2>{ isEn ? "Reviews" : "评荐" }</h2>
                                {moreReviews}
                            </div>
                        }

                    </div>
                </main>
            </div>
        )
    }
}